import React from 'react'
//import { Link } from 'react-router-dom'
import Header from '../Header'
import Footer from '../Footer'
import {Container,Row, Card, CardDeck, Button, Jumbotron } from 'react-bootstrap'
import AoAn from '../assets/AoAn.jpg'
import CAS from '../assets/cas.jpg'
export default function ServicesPage() {
  return (
    <Container className="container">
    <Header />
      <Jumbotron className = "Lead-text">
    <h1>Services</h1>
    <p>
      We provide a number of treatments and services to suit the needs
       of our patients.
    </p>
    <p>
      See below for your treatment options.
    </p>
  </Jumbotron>
  <Row>
  <CardDeck>
  <Card>
    <Card.Img
      height = "100px"
      width = "160px"
      variant="top"
      src={AoAn} />
    <Card.Body>
      <Card.Title>Aortic Aneurysm</Card.Title>
      <Card.Text>
      This condition is an enlargement of the aorta, the main blood vessel that deliver blood to the body, at the level of the abdomen.
    </Card.Text>
      <Button variant="primary">Learn More</Button>
    </Card.Body>
  </Card>
  <Card>
    <Card.Img
      height = "100px"
      width = "160px"
      variant="top"
      src={CAS} />
    <Card.Body>
      <Card.Title>Carotid Artery Disease</Card.Title>
      <Card.Text>
      Narrowing of the blood vessels in the neck that carries blood from the heart to the brain.
    </Card.Text>
      <Button variant="primary">Learn More</Button>
    </Card.Body>
  </Card>
  <Card>
      <Card.Img
        height = "160px"
        width = "160px"
        variant="top"
        src={AoAn} />
      <Card.Body>
        <Card.Title>Deep Vein Thrombosis</Card.Title>
        <Card.Text>
        A blood clot in deep vein, usually in the legs.
      </Card.Text>
        <Button variant="primary">Learn More</Button>
      </Card.Body>
    </Card>
    <Card>
        <Card.Img
          height = "160px"
          width = "160px"
          variant="top"
          src={AoAn} />
        <Card.Body>
          <Card.Title>Dialysis Access Managment</Card.Title>
          <Card.Text>
          Dialysis is the clinical purification of blood by dialysis, as a substitute for the normal dfunction of the kidney.
        </Card.Text>
          <Button variant="primary">Learn More</Button>
        </Card.Body>
      </Card>
  </CardDeck>
  <br />
</Row>
  <Row>
    <CardDeck>
    <Card>
      <Card.Img
        height = "100px"
        width = "160px"
        variant="top"
        src={AoAn} />
      <Card.Body>
        <Card.Title>Aortic Aneurysm</Card.Title>
        <Card.Text>
        This condition is an enlargement of the aorta, the main blood vessel that deliver blood to the body, at the level of the abdomen.
      </Card.Text>
        <Button variant="primary">Learn More</Button>
      </Card.Body>
    </Card>
    <Card>
      <Card.Img
        height = "100px"
        width = "160px"
        variant="top"
        src={CAS} />
      <Card.Body>
        <Card.Title>Carotid Artery Disease</Card.Title>
        <Card.Text>
        Narrowing of the blood vessels in the neck that carries blood from the heart to the brain.
      </Card.Text>
        <Button variant="primary">Learn More</Button>
      </Card.Body>
    </Card>
    <Card>
        <Card.Img
          height = "160px"
          width = "160px"
          variant="top"
          src={AoAn} />
        <Card.Body>
          <Card.Title>Aortic Aneurysm</Card.Title>
          <Card.Text>
          This condition is an enlargement of the aorta, the main blood vessel that deliver blood to the body, at the level of the abdomen.
        </Card.Text>
          <Button variant="primary">Learn More</Button>
        </Card.Body>
      </Card>
      <Card>
          <Card.Img
            height = "160px"
            width = "160px"
            variant="top"
            src={AoAn} />
          <Card.Body>
            <Card.Title>Aortic Aneurysm</Card.Title>
            <Card.Text>
            This condition is an enlargement of the aorta, the main blood vessel that deliver blood to the body, at the level of the abdomen.
          </Card.Text>
            <Button variant="primary">Learn More</Button>
          </Card.Body>
        </Card>
    </CardDeck>
  </Row>
      <Footer />
    </Container>
  )
}
